@use 'base/variables' as v;
@use 'base/mixins' as m;

.promo {
    background-image: url(../img/promo-bg.png);
    padding: 10rem 0;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    background-attachment: fixed;

    &::before {
        content: '';
        background-color: v.$negro;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0.4;
    }

    &__contenedor {
        position: relative;
        @include m.tablet {
            display: flex;
            align-items: center;
        }
    }

    &__parrafo {
        color: v.$blanco;
        font-weight: 700;
        font-size: 3rem;
        line-height: 2;
    
    }

    &__enlace {
        @include m.boton(v.$gris, v.$blanco);
        display: block;
        
        @include m.tablet {
            width: 30rem;           
        }
    }
}