.navegacion {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    visibility: visible;
    opacity: 1;
    display: flex;
    flex-direction: row;
  }
}
.navegacion__enlace {
  margin: 1rem 0;
}
.navegacion__enlace::after {
  content: "";
  display: block;
  background-color: #000;
  width: 0%;
  margin: 0 auto;
  height: 1px;
  transition: 0.3s ease-out;
}
.navegacion__enlace:hover::after {
  width: 100%;
}
.navegacion__enlace--cita {
  width: 100%;
  margin: 1rem 0;
  padding: 1.2rem;
  font-weight: 700;
  background-color: #c1b498;
  color: #FFFFFF;
  text-align: center;
  min-width: 20rem;
  text-transform: uppercase;
  transition-property: background-color;
  transition-duration: 0.2s;
}
@media (min-width: 768px) {
  .navegacion__enlace--cita {
    width: auto;
  }
}
.navegacion__enlace--cita:hover {
  background-color: #ae9d78;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin: 0 2.5rem 0 0;
  }
}
@media (min-width: 1024px) {
  .navegacion__enlace {
    margin: 0 5rem 0 0;
  }
}

