@use 'variables' as v; 
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
}

p {
    font-size: 2rem;
    font-family: v.$fuentePrincipal;
}

a {
    color: v.$negro;
}

[class$="contenedor"],
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.contenedor-grande {
    width: 95%;
    margin: 0 auto;
}

img {
    max-width: 100%;
    display: block;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}
.seccion {
    padding: 6rem 0;
    // border-bottom: 2px solid #d7d7d7;

    @include m.tablet {
        padding: 8rem 0;  
    }
}
