@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimoniales {
    background-image: url(../img/bg-image.png);


    &-nobg {
        background-image: none;
    }

    &__heading {
        @include m.heading;
    }

    &__grid {
        @include m.tablet {
            display: flex;        
        }
    }

    &__enlace-cont {
        margin-top: 2rem;
        display: flex;
        justify-content: center;
    }

    &__enlace {
        @include m.enlace;
    }
}

.mt-20 {
    @include m.tablet {
        margin-top: 20rem;
    }
}

.mt-10 {
    @include m.tablet {
        margin-top: 10rem;
    }
}




.testimonial {
    box-shadow: 16px 16px 0px -7px v.$secundario;
    padding: 2rem;
    margin-bottom: 2rem;

    :last-of-type {
        margin-bottom: 0;
    }

    @include m.tablet {
        margin-bottom: 0;
        max-height: 62rem;
    }

    @include m.desktop {
        max-height: 75rem;
    }

    &__imagen {

    }

    &__texto {
        position: relative;

        &::before {
            content: url(../img/comillas.svg);
            display: block;
            height: 8rem;
            width: 8rem;
            position: absolute;
            top: -4rem;
            right: 2rem;

            @include m.tablet {
                height: 6rem;
                width: 6rem;
                top: -8rem;
                right: 0;
            }

            @include m.desktop {
                top: -4rem;
                right: 2rem;
            }
        }
    }

    &__nombre {
        font-weight: 700;
        font-size: 2.4rem;
    }

    &__parrafo {
        line-height: 2;
        color: darken(v.$primario, 40);
        text-align: justify;
    }

    &__estrellas {

    }
}