@use '../base/variables' as v;
@use '../base/mixins' as m;

.header {
    background-color: lighten(v.$primario, 3);


    &__grid {
        padding: 5rem 0;

        @include m.tablet {
            padding-top: 0rem;            
        }

        @include m.desktop {
            @include m.grid(2, 2rem);
            padding-top: 5rem;
            padding-bottom: 0rem;
        }

        @include m.desktopXL {
            padding-top: 5rem;
            
        }
    }

    &__realza {
        padding-top: 3rem;
    }

    &__parrafo {
        margin: 0;

        &--grande {
            margin: 2rem 0;
            font-size: 6rem;
            font-weight: 700;
            font-family: v.$fuenteTerciaria;
        }
    }

    &__lista {
        @include m.desktop {
            display: flex;
        }

        &-item {
            display: flex;
            @include m.desktop {
                margin-right: 1rem;
            }
        }

        &-imagen {
            width: 2.5rem;
        }

        &-parrafo {
            margin-left: 1rem;
            font-size: 1.6rem;

            @include m.desktop {
                margin-left: .5rem;
                font-size: 1.6rem;
                max-width: 14rem;
            }

            &::after {
                content: '';
                display: block;
                background-color: v.$negro;
                width: 0%;
                margin: 0 auto;
                height: 1px;
                transition: 0.3s ease-out;
            }
    
            &:hover::after {
                width: 100%;
            }

        }
    }

    &__imagen-cont {
        @include m.desktop {
            margin-bottom: -5rem;
            margin-top: 5rem;
            background-color: lighten(v.$primario, 3);
            border: 1px solid darken(v.$primario, 30%);
            padding: 2rem;
        }
    }

    &__imagen {
        background-color: darken(v.$primario, 10%);
    }

    &__img {
        margin: 0 auto;
    }
}