@use 'base/variables' as v;
@use 'base/mixins' as m;

.agendar {
    padding: 5rem 0;

    &__img {
        @include m.tablet {
            padding-top: 5rem;
            margin: 0 auto;
        }
    }

    &__parrafo {
        color: darken(v.$primario, 60%);
        font-size: 1.8rem;
    }

    &__flex {
        @include m.tablet {
            display: flex;
            gap: 3rem;
        }
    }

    &__campo {
        @include m.tablet {
            width: 50%;
        }

        &--text {
            display: block;
        }
    }

    &__label {
        color: darken(v.$primario, 60%);
    }

    &__input {
        display: block;
        padding: 1rem;
        margin: 1rem 0;
        border: 1px solid #dde3ec;
        width: 100%;

    }

    &__textarea {
        margin: 1rem 0;
        border: 1px solid #dde3ec;
        width: 100%
    }

    &__btn {
        @include m.boton(v.$secundario, v.$blanco);
        border: none;
        cursor: pointer;
        display: block;
        width: 100%;

        &--opacity {
            opacity: 50%;
            cursor: default;

            &:hover {
                background-color: v.$secundario;
            }
        }
    }
}

.error {
    background-color: #f44336;
    color: v.$blanco;
    padding: 1rem;
    font-family: v.$fuentePrincipal;
    border: 1px solid darken($color: #f44336, $amount: 10%);
    text-align: center;
}

.exito {
    background-color: rgb(2, 172, 2);
    color: v.$blanco;
    padding: 1rem;
    font-family: v.$fuentePrincipal;
    border: 1px solid darken(rgb(2, 172, 2), 10%);
    text-align: center;
}


//-------------- SPINNER -------------

.spinner__contenedor {
    display: flex;
    justify-content: center;
    margin-top: 5rem;
}

.hidden {
    visibility: hidden;
    display: none;
}

.sk-chase {
    width: 40px;
    height: 40px;
    position: relative;
    animation: sk-chase 2.5s infinite linear both;
  }
  
  .sk-chase-dot {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0; 
    animation: sk-chase-dot 2.0s infinite ease-in-out both; 
  }
  
  .sk-chase-dot:before {
    content: '';
    display: block;
    width: 25%;
    height: 25%;
    background-color: v.$secundario;
    border-radius: 100%;
    animation: sk-chase-dot-before 2.0s infinite ease-in-out both; 
  }
  
  .sk-chase-dot:nth-child(1) { animation-delay: -1.1s; }
  .sk-chase-dot:nth-child(2) { animation-delay: -1.0s; }
  .sk-chase-dot:nth-child(3) { animation-delay: -0.9s; }
  .sk-chase-dot:nth-child(4) { animation-delay: -0.8s; }
  .sk-chase-dot:nth-child(5) { animation-delay: -0.7s; }
  .sk-chase-dot:nth-child(6) { animation-delay: -0.6s; }
  .sk-chase-dot:nth-child(1):before { animation-delay: -1.1s; }
  .sk-chase-dot:nth-child(2):before { animation-delay: -1.0s; }
  .sk-chase-dot:nth-child(3):before { animation-delay: -0.9s; }
  .sk-chase-dot:nth-child(4):before { animation-delay: -0.8s; }
  .sk-chase-dot:nth-child(5):before { animation-delay: -0.7s; }
  .sk-chase-dot:nth-child(6):before { animation-delay: -0.6s; }
  
  @keyframes sk-chase {
    100% { transform: rotate(360deg); } 
  }
  
  @keyframes sk-chase-dot {
    80%, 100% { transform: rotate(360deg); } 
  }
  
  @keyframes sk-chase-dot-before {
    50% {
      transform: scale(0.4); 
    } 100%, 0% {
      transform: scale(1.0); 
    } 
  }