@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: #252424;
    padding: 3rem 3rem 0;

    @include m.tablet {
        padding: 5rem 0 0 0;
    }

    &__contenedor {
        @include m.desktop {
            @include m.grid(2, 5rem);     
        }
    }

    &__sigueme {

    }

    &__heading {
        color: v.$blanco;
        font-family: v.$fuenteTerciaria;
        
    }

    &__enlaces {
        display: flex;

        @include m.tablet {
            display: inline;
        }

        svg {
            color: v.$blanco;
            transition: .2s ease-in;
            margin-right: 2rem;

            &:hover {
                color: darken(v.$secundario, 20);
            }
        }
    }

    &__contacto {

    }

    &__parrafo {
        color: v.$gris;
        font-family: v.$fuenteTerciaria;
        line-height: 2;
        font-size: 1.4rem;

        &--copyright {
            margin-bottom: 0;
        }
    }

    .navegacion {
        margin: 3rem 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;

        @include m.tablet {
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
        }
        
        &__enlace {
            color: v.$blanco;

            &::after {
                background-color: v.$blanco;
            }
        }

        
    }

}