@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {

    &__heading {
        @include m.heading
    }

    &__enlace-cont {
        margin-top: 2rem;
        display: flex;
        justify-content: center;
    }

    &__enlace {
        @include m.enlace;
    }

    &__grid {
        @include m.tablet {
            @include m.grid(2, 5rem);
        }

        @include m.desktop {
            @include m.grid(3, 5rem);
        }
    }

    &__imagen {
        margin-bottom: 5rem;

        @include m.tablet {
            margin-bottom: 0;
        }
    }

    &__img {
        width: 30rem;
        height: 25rem;
        object-fit: cover;
        margin: 0 auto;
        cursor: pointer;

        @include m.tablet {
            width: 35rem;
            height: 30rem;
            transition: 0.3s;

            &:hover {
                opacity: 0.7;
            }
        }
    }
}

.slider {
    overflow: hidden;
    position: relative;

    &__slide {
        flex-shrink: 0;
        height: 100%;
        width: 100%;
        position: relative;
        transition-property: transform;
    }

    &__imagen {
        height: 50rem;
        width: 60rem;
        object-fit: cover;
        margin: 0 auto;
    }
}

.modal { /* El background */
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}

.modal-content {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
}

#caption {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
    text-align: center;
    color: v.$blanco;
    padding: 2rem 0;
    font-size: 2.4rem;
    font-family: v.$fuenteTerciaria;
}

.modal-content, #caption {
    animation-name: zoom;
    animation-duration: 0.6s;
}

@keyframes zoom {
    from {transform:scale(0)}
    to {transform:scale(1)}
}

.close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}