@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros {
    // background-image: url(../img/bg-image.png);
    @include m.tablet {
        padding-top: 5rem;
  
    }

    &__heading {
        @include m.heading;
    }

    &__parrafo {
        margin: 3rem 0;
        line-height: 2;
        color: darken(v.$primario, 40);
    }

    &__enlace-cont {
        display: flex;
        justify-content: center;
    }

    &__enlace {
        @include m.enlace;
    }

    &__imagenes {
        padding: 5rem 0;
        @include m.tablet {
            @include m.grid(3, 1rem);
        }
    }

    &__imagen {
        margin-bottom: 2rem;
        // border: 1px solid darken(v.$secundario, 30);
        padding: 1rem;
        display: flex;
        box-shadow: 16px 16px 0px -7px v.$secundario;
        transition: .35s ease-in-out;


        &:hover {
            transform: scale(1.05);
        }

        @include m.tablet {
            margin: 0;
        }
        
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}

.sticky + .espacio {
    @include m.tablet {
        padding-top: 10rem;
    }

    @include m.desktop {
        padding-top: 10rem; 
    }
}