@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    margin: 1rem 0;
    padding: 1.2rem;
    font-weight: 700;
    background-color: $bgColor;
    color: $colorTexto;
    text-align: center;
    min-width: 20rem;
    text-transform: uppercase;
    transition-property: background-color;
    transition-duration: .2s;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken($bgColor, 10%);
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin heading {
    text-align: center;
    color: v.$negro;
    font-family: v.$fuentePrincipal;
    font-weight: 400;
    font-size: 3rem;
    span {
        color: v.$negro;
        display: block;
        font-size: 5rem;
        font-weight: 400;
        font-family: v.$fuenteTerciaria;
    }

    &::after {
        content: '';
        background-color: v.$secundario;
        height: .5rem;
        width: 50%;
        display: block;
    }
}

@mixin enlace {
    font-size: 2.5rem;
    font-weight: 700;
    text-align: center;
    transition: .2s ease-in-out;


    &:hover {
        transform: scale(1.1);
