@mixin background {
    background-image: url(../img/sparkles.svg);
    background-repeat: no-repeat;
    background-position: 98% 45%;
    background-size: 8rem;

    @include tablet {
        background-image: url(../img/eyelashes.svg), url(../img/sparkles.svg);
        background-position: 5% 5%, 100% 75%;
        background-size: 10rem, 10rem;
    }

    @include desktop {
        background-position: 5% 5%, 100% 55%;
        background-size: 14rem, 14rem;
    }
}