@use '../base/variables' as v;
@use '../base/mixins' as m;

.header__barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    background-color: #f5f5f5;
    width: 100%;
    z-index: 3;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-around;
    }
}

.header__logo {
    width: 15rem;
    margin: 0 auto 2rem auto;

    @include m.tablet {
        margin: 0 2rem 0 0;
    }

    @include m.desktop {
        margin: 0;
    }
}

.sticky {
    @include m.tablet {
        position: fixed;
        top: 0;
        width: 100%;
    }

}

.sticky + .header__grid {
    @include m.tablet {
        padding-top: 10rem;
    }

    @include m.desktop {
        padding-top: 15rem;
        
    }
}

.no-sticky {
    @include m.tablet {
        position: static;
    }
}

.no-sticky + .header__grid {
    @include m.tablet {
        padding-top: 0rem;
    }

    @include m.desktop {
        padding-top: 0rem;
        
    }
}