@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    height: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .3s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        visibility: visible;
        opacity: 1;
        display: flex;
        flex-direction: row;
    }

    &__enlace {
        margin: 1rem 0;

        &::after {
            content: '';
            display: block;
            background-color: v.$negro;
            width: 0%;
            margin: 0 auto;
            height: 1px;
            transition: 0.3s ease-out;
        }

        &:hover::after {
            width: 100%;
        }

        &--cita {
            width: 100%;
            @include m.boton(v.$secundario, v.$blanco);
        }

        @include m.tablet {
            margin: 0 2.5rem 0 0;
        }

        @include m.desktop {
            margin: 0 5rem 0 0;
        }
    }
}

.mobile-menu {   // Men de hamburguesa
    padding-top: 3rem;
    cursor: pointer;

    @include m.tablet {
        display: none;
    }

    svg {
        width: 6rem;
    }
}

.mostrar {
    height: 100%;
    visibility: visible;
    opacity: 1;
}

