@use 'base/variables' as v;
@use 'base/mixins' as m;

.conoce {
    &__grid {
        @include m.tablet {
            @include m.grid(2, 5rem);
            align-items: center
        }
    }

    &__header {
        font-family: v.$fuenteTerciaria;
        font-size: 6rem;
        margin: 1rem 0;

        &-gris {
            text-transform: uppercase;
            color: v.$gris;
            font-size: 2.5rem;
            font-style: italic;
        }
    }

    &__parrafo {
        line-height: 1.6;
    }

    &__enlaces {
        text-align: center;

        svg {
            color: v.$secundario;
            transition: .2s ease-in;
            margin-right: 2rem;
    
            &:hover {
                color: darken(v.$secundario, 20);
            }
        }
    }


}

