@use 'base/mixins' as m;
@use 'base/variables' as v;

.servicios {
    background-image: url(../img/bg-image.png);


    &-nobg {
        background-image: none;
    }

    &__contenedor {
        display: flex;
        flex-direction: column;

        @include m.tablet {
            // background-image: url(../img/sparkles.svg), url(../img/sparkles.svg);
            // background-position: 15% 15%, 15% 85%;
            @include m.grid(2, 4rem);
        }
    
        @include m.desktop {
            // background-position: 5% 10%, 15% 100%;
            // background-size: 14rem, 14rem;
        }
    }


    // @include m.background;



    &__imagen {
        order: 2;
        box-shadow: 16px 16px 0px -7px v.$secundario;
        transition: .35s ease-in-out;

        @include m.tablet {
            order: 1;
            margin: auto auto;
        }

        &:hover {
            transform: scale(1.05);
        }
    }

    &__texto {
        order: 1;

        @include m.tablet {
        order: 2;
        }
    }

    &__heading {
        @include m.heading;
    }

    &__parrafo {
        margin: 3rem 0;
        line-height: 2;
        color: darken(v.$primario, 40);
    }

    &__enlace-cont {
        display: flex;
        justify-content: center;
    }

    &__enlace {
        @include m.enlace;
    }

    &__boton {
        @include m.boton(v.$secundario, v.$blanco);
        display: block;
        margin: 3rem 0;
    }


}

.pagina-servicios {

    &__heading {
        @include m.heading;
    }

    &__servicios {
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }

    &__servicio {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 7rem;
    }

    &__imagen-cont {

    }

    &__img {
        width: 35rem;
        height: 35rem;
        object-fit: cover;

        @include m.desktop {
            width: 55rem;
            height: 55rem;
        }
    }

    &__informacion {
        border: 1px solid v.$primario;
        background-color: v.$blanco;
        width: 75%;
        margin-top: -5rem;
        text-align: center;
        padding: 2rem;
        box-shadow: 0px 0px 28px 1px rgba(0,0,0,0.48);
    }

    &__precio {
        font-family: v.$fuenteTerciaria;
        font-size: 4rem;
        margin: 0;
    }

    &__descripcion {
        font-family: v.$fuenteTerciaria;
        font-size: 2.9rem;
        margin: 1rem 0;
    }

    &__enlace {
        font-size: 2rem;
        color: v.$gris;
    }

}